import * as gm from 'gammacv';
import canvasDrawChart from '../lib';

const plotCanvas = document.getElementById('plot');
const scatterCanvas = document.getElementById('scatter');
const histogramCanvas = document.getElementById('histogram');

const dimension = 6;
const samplesCount = 10;
const tensorArray = [];

const plotStyles = {
  dataStyles: [
    {
      lineWidth: 2,
      lineColor: 'rgba(20, 110, 255, 0.2)',
      pointSize: 5,
      pointColor: '#146eff',
      pointBorder: '#ffffff',
    }, {
      lineWidth: 2,
      lineColor: 'rgba(252, 208, 15, 0.2)',
      pointSize: 5,
      pointColor: 'rgb(252, 208, 15)',
      pointBorder: '#ffffff',
    }, {
      lineWidth: 2,
      lineColor: 'rgba(168, 168, 168, 0.2)',
      pointSize: 5,
      pointColor: 'rgb(168, 168, 168)',
      pointBorder: '#ffffff',
    },
  ],
  showRulerY: false,
  font: 'Open Sans',
  legendGrid: [3, 10],
  legendFontSize: 10,
  legendFontColor: 'rgba(7, 15, 82, 0.8)',
  legendGuidesColor: 'rgba(7, 15, 82, 0.1)',
  legendRulerColor: 'rgba(7, 15, 82, 0.4)',
  indent: {
    xAxis: 40,
    yAxis: 22,
    xMark: 15,
    yMark: 6,
  },
  xAxisLabels: ['mon', 'tue', 'wed', 'thu', 'fri', 'sat', 'sun'],
  yAxisFromMinVal: true,
  showMinY: false,
  roundMax: true,
};

const scatterStyles = {
  dataStyles: [
    {
      pointSize: 5,
      pointColor: '#146eff',
      pointBorder: '#ffffff',
    }, {
      pointSize: 5,
      pointColor: 'rgb(252, 208, 15)',
      pointBorder: '#ffffff',
    }, {
      pointSize: 5,
      pointColor: 'rgb(168, 168, 168)',
      pointBorder: '#ffffff',
    },
  ],
  showRulerY: false,
  font: 'Open Sans',
  legendGrid: [3, 10],
  legendFontSize: 10,
  legendFontColor: 'rgba(7, 15, 82, 0.8)',
  legendGuidesColor: 'rgba(7, 15, 82, 0.1)',
  legendRulerColor: 'rgba(7, 15, 82, 0.4)',
  indent: {
    xAxis: 40,
    yAxis: 22,
    xMark: 15,
    yMark: 6,
  },
  yAxisFromMinVal: true,
  showMinY: false,
  roundMax: true,
};

const histStyles = {
  dataStyles: [
    {
      bgColor: '#146eff',
    }, {
      bgColor: 'rgb(252, 208, 15)',
    }, {
      bgColor: 'rgb(168, 168, 168)',
    },
  ],
  showRulerY: false,
  font: 'Open Sans',
  legendGrid: [3, samplesCount],
  legendFontSize: 10,
  legendFontColor: 'rgba(7, 15, 82, 0.8)',
  legendGuidesColor: 'rgba(7, 15, 82, 0.1)',
  legendRulerColor: 'rgba(7, 15, 82, 0.4)',
  indent: {
    xAxis: 40,
    yAxis: 22,
    xMark: 15,
    yMark: 6,
  },
  xAxisLabels: ['mon', 'tue', 'wed', 'thu', 'fri', 'sat', 'sun'],
  yAxisFromMinVal: true,
  showMinY: false,
  roundMax: true,
};

for (let a = 0; a < dimension; a += 1) {
  const tensor = new gm.Tensor('float32', [samplesCount, 2]);

  for (let i = 0; i < samplesCount; i += 1) {
    tensor.set(i, 0, i);
    tensor.set(i, 1, Math.random() * 20);
  }

  tensorArray.push(tensor);
}

canvasDrawChart(tensorArray, plotStyles, plotCanvas, 'plot');
canvasDrawChart(tensorArray, scatterStyles, scatterCanvas, 'scatter');
canvasDrawChart(tensorArray, histStyles, histogramCanvas, 'histogram');
