export default function isTensor2D(shape) {
  if (shape.length !== 2) {
    throw new Error(`Invalid rank, expected two-dimensional tensor
      for example - [100, 2]
      actually - [${shape}]`);
  }
  if (shape[1] !== 2) {
    throw new Error(`Invalid second shape argument
      expected - 2
      actually - ${shape[1]}`);
  }
}
