import * as gm from 'gammacv';
import isTensor2D from './utils';

const defaultStyles = {
  dataStyle: {
    lineWidth: null,
    lineColor: '#cccccc',
    pointSize: 4,
    pointColor: '#ff0000',
    pointBorder: null,
    bgColor: null,
  },
  legendGrid: [3, 7],
  indent: {
    xAxis: 30,
    yAxis: 40,
    xMark: 10,
    yMark: 5,
  },
  bottomPadding: 20,
  size: [800, 400],
};

const findMinMax = (axis, tensor) => {
  let min = Infinity;
  let max = -Infinity;

  for (let i = 0; i < tensor.shape[0]; i += 1) {
    const elem = tensor.get(i, axis === 'x' ? 0 : 1);

    if (elem < min) min = elem;
    if (elem > max) max = elem;
  }

  return { min, max };
};

/**
 *
 * @param {string} axis
 * @param {object} canvas
 * @param {array} tensorArray
 * @param {object} styles
 */
const drawAxisGrid = (axis, canvas, { min, max, roundedMax }, styles, type) => {
  const {
    font,
    legendFontSize,
    legendFontColor,
    legendRulerColor,
    legendGuidesColor,
    xAxisLabels,
    showRulerY,
    indent: styleIndent,
    yAxisFromMinVal,
    legendGrid,
    showMinY,
  } = styles;
  let { bottomPadding } = defaultStyles;
  bottomPadding = type === 'histogram' ? 0 : bottomPadding;

  const isXAxis = axis === 'x';
  const isYAxis = axis === 'y';

  const axisGrid = legendGrid
    ? legendGrid[isXAxis ? 1 : 0]
    : defaultStyles.legendGrid[isXAxis ? 1 : 0];

  const indent = styleIndent || defaultStyles.indent;

  const axisStep = ((isXAxis ? canvas.width : canvas.height - bottomPadding) - indent[`${axis}Axis`] * 2) / axisGrid;

  const legendStep = (
    isYAxis
      ? (isYAxis ? roundedMax : max) - min
      : max
  ) / (isXAxis ? axisGrid - 1 : axisGrid);

  const precision = legendStep.toString().split('.')[1];
  const digits = precision ? precision.length : 0;

  const axisLegendValues = new Array(axisGrid);
  let tempLegendValue = min;

  for (let i = 0; i < axisGrid; i += 1) {
    let val = 0;

    if (i === 0) {
      val = yAxisFromMinVal ? min : 0;
    } else {
      tempLegendValue += legendStep;
      val = tempLegendValue;
    }

    axisLegendValues[i] = (val.toFixed(digits >= 2 ? 2 : digits));
  }

  for (let i = 0; i < axisGrid; i += 1) {
    const tempX = isXAxis
      ? axisStep * i + indent.xAxis
      : indent.xAxis;
    const tempY = isXAxis
      ? canvas.height - indent.yAxis
      : canvas.height - (axisStep * i + indent.yAxis);

    if ((showRulerY && isYAxis) || isXAxis) {
      /* draw rulers */
      gm.canvasDrawLine(canvas, [
        tempX,
        isXAxis ? tempY : tempY - bottomPadding,
        isXAxis ? tempX + axisStep : tempX,
        isXAxis ? tempY : tempY - axisStep - bottomPadding,
      ], legendRulerColor);

      /* draw division marks */
      gm.canvasDrawLine(canvas, [
        tempX,
        isXAxis ? tempY : tempY - bottomPadding,
        isXAxis ? tempX : indent.xAxis - 10,
        isXAxis ? canvas.height - indent.yAxis + 10 : tempY - bottomPadding,
      ], legendRulerColor);

      /* draw last mark */
      if (i === axisGrid - 1) {
        gm.canvasDrawLine(canvas, [
          isXAxis ? tempX + axisStep : tempX,
          isXAxis ? tempY : tempY - axisStep - bottomPadding,
          isXAxis ? tempX + axisStep : indent.xAxis - 10,
          isXAxis ? canvas.height - indent.yAxis + 10 : tempY - axisStep - bottomPadding,
        ], legendRulerColor);
      }
    }

    /* draw guides */
    if ((isYAxis && i !== 0) || (isXAxis && type !== 'histogram')) {
      gm.canvasDrawLine(canvas, [
        isXAxis ? tempX + axisStep / 2 : tempX,
        isXAxis ? tempY : tempY - bottomPadding,
        isXAxis ? tempX + axisStep / 2 : canvas.width - indent.xAxis,
        isXAxis ? indent.yAxis : tempY - bottomPadding,
      ], legendGuidesColor);
    }

    /* draw last Y guide */
    if (i === axisGrid - 1 && isYAxis) {
      gm.canvasDrawLine(canvas, [
        tempX,
        tempY - axisStep - bottomPadding,
        canvas.width - indent.xAxis,
        tempY - axisStep - bottomPadding,
      ], legendGuidesColor);
    }

    const ctx = canvas.getContext('2d');
    const markTxt = (xAxisLabels && xAxisLabels[i] && isXAxis)
      ? xAxisLabels[i].toUpperCase()
      : axisLegendValues[i];

    ctx.beginPath();
    ctx.fillStyle = legendFontColor;
    ctx.font = `${legendFontSize}pt ${font}`;
    ctx.textAlign = isXAxis ? 'center' : 'end';
    ctx.textBaseline = 'middle';

    if (!showMinY && i === 0 && isYAxis) {
      continue;
    } else {
      ctx.fillText(
        markTxt,
        isXAxis ? tempX + axisStep / 2 : indent.xAxis - indent.yMark,
        isXAxis ? canvas.height - indent.yAxis + indent.xMark : tempY - bottomPadding,
      );
    }

    if (i === axisGrid - 1 && isYAxis) {
      ctx.fillText(
        roundedMax,
        indent.xAxis - indent.yMark,
        tempY - axisStep - bottomPadding,
      );
    }
  }
};

/**
 *
 * @param {number} index
 * @param {object} canvas
 * @param {tensor} tensor
 * @param {object} styles
 * @param {object} maxChart
 */
const drawData = (index, dataCount, canvas, tensor, styles, { x, y }, type) => {
  const {
    dataStyles,
    indent: styleIndent,
    yAxisFromMinVal,
    legendGrid,
  } = styles;
  const { bottomPadding } = defaultStyles;

  const dataStyle = (dataStyles && dataStyles[index])
    ? dataStyles[index]
    : defaultStyles.dataStyle;

  const {
    lineWidth,
    lineColor,
    pointSize,
    pointBorder,
    pointColor,
  } = dataStyle;

  const chartScale = (y.max - y.min) * 100 / y.roundedMax;

  const indent = styleIndent || defaultStyles.indent;

  const tempYMin = (yAxisFromMinVal || y.min < 0) ? y.min : 0;

  const gridHeight = canvas.height - indent.yAxis * 2;
  const drawHeight = ((gridHeight - bottomPadding) * chartScale / 100);

  const xGrid = legendGrid ? legendGrid[1] : defaultStyles.legendGrid[1];
  const xStep = (canvas.width - indent.xAxis * 2) / xGrid;

  const normX = new Array(tensor.shape[0]);
  const normY = new Array(tensor.shape[0]);

  for (let i = 0; i < tensor.shape[0]; i += 1) {
    normX[i] = (tensor.get(i, 0) - x.min) / // eslint-disable-line
      (x.max - x.min) * (canvas.width - indent.xAxis * 2 - xStep);
    normY[i] = canvas.height - ((tensor.get(i, 1) - tempYMin) / // eslint-disable-line
      (y.max - tempYMin) * drawHeight);
  }

  if (type !== 'scatter' && type !== 'histogram') {
    for (let i = 0; i < tensor.shape[0]; i += 1) {
      gm.canvasDrawLine(canvas, [
        normX[i] + indent.xAxis + xStep / 2,
        normY[i] - bottomPadding - indent.yAxis,
        normX[i + 1] + indent.xAxis + xStep / 2,
        normY[i + 1] - bottomPadding - indent.yAxis,
      ], lineColor, lineWidth);
    }
  }

  if (type === 'histogram') {
    const columnWidth = xStep / dataCount;

    for (let i = 0; i < tensor.shape[0]; i += 1) {
      gm.canvasDrawLine(canvas, [
        indent.xAxis + xStep * i + columnWidth / 2 + columnWidth * index,
        normY[i]/*  - bottomPadding */ - indent.yAxis,
        indent.xAxis + xStep * i + columnWidth / 2 + columnWidth * index,
        canvas.height/*  - bottomPadding */ - indent.yAxis,
      ], dataStyle.bgColor, columnWidth);
    }
  }

  if (type !== 'histogram') {
    for (let i = 0; i < tensor.shape[0]; i += 1) {
      gm.canvasFillCircle(canvas, [
        normX[i] + indent.xAxis + xStep / 2,
        normY[i] - bottomPadding - indent.yAxis,
      ], pointSize, pointColor);
      if (pointBorder) {
        gm.canvasDrawCircle(canvas, [
          normX[i] + indent.xAxis + xStep / 2,
          normY[i] - bottomPadding - indent.yAxis,
        ], pointSize + 1, pointBorder);
      }
    }
  }
};

/**
 *
 * @param {array} tensorArray
 * @param {object} styles
 */
const canvasDrawChart = (tensorArray, styles, sourceCanvas, type) => {
  const {
    size: styleSize,
    legendGrid,
  } = styles;
  const size = styleSize || defaultStyles.size;

  const canvas = sourceCanvas || gm.canvasCreate();

  canvas.width = size[0] * window.devicePixelRatio;
  canvas.height = size[1] * window.devicePixelRatio;

  canvas.style.width = `${size[0]}px`;
  canvas.style.height = `${size[1]}px`;

  canvas.getContext('2d').scale(window.devicePixelRatio, window.devicePixelRatio);

  const fakeCanvas = {
    width: size[0],
    height: size[1],
    getContext: () => canvas.getContext('2d'),
  };

  const boundaries = {
    x: {
      min: Infinity,
      max: -Infinity,
    },
    y: {
      min: Infinity,
      max: -Infinity,
      roundedMax: null,
    },
  };

  for (let i = 0; i < tensorArray.length; i += 1) {
    Object.keys(boundaries).forEach((key) => {
      const { min, max } = findMinMax(key, tensorArray[i]);
      if (min < boundaries[key].min) boundaries[key].min = min;
      if (max > boundaries[key].max) boundaries[key].max = max;
    });
  }

  boundaries.y.roundedMax = Math.ceil((boundaries.y.max + 1) / legendGrid[0]) * legendGrid[0];

  Object.keys(boundaries).forEach((key) => {
    drawAxisGrid(key, fakeCanvas, boundaries[key], styles, type);
  });

  for (let i = 0; i < tensorArray.length; i += 1) {
    isTensor2D(tensorArray[i].shape);
    drawData(i, tensorArray.length, fakeCanvas, tensorArray[i], styles, boundaries, type);
  }
};

export default canvasDrawChart;
